body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.no-midday {
  -webkit-animation: no-midday infinite 5s linear;
          animation: no-midday infinite 5s linear;
  height: 5vmin;
  color: red;
  font-weight: bold;
  font-size: 64px;
  pointer-events: none;
  display: inline-block;
  padding: 0 40px;
}
.no-midday-2 {
  display: inline-block;
  padding: 0 40px;
  -webkit-animation: no-midday-2 infinite 5s linear;
          animation: no-midday-2 infinite 5s linear;
  height: 5vmin;
  color: red;
  font-weight: bold;
  font-size: 64px;
  pointer-events: none;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes no-midday {
  from {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }
  to {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
  }
}

@keyframes no-midday {
  from {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }
  to {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
  }
}
@-webkit-keyframes no-midday-2 {
  from {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
  to {
    -webkit-transform: rotateY(540deg);
            transform: rotateY(540deg);
  }
}
@keyframes no-midday-2 {
  from {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
  to {
    -webkit-transform: rotateY(540deg);
            transform: rotateY(540deg);
  }
}

